/* === Container === */
.uh-container {
  padding: 20px;
  font-family: Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  box-sizing: border-box;
}

/* Welcome Text */
.uh-welcome {
  font-size: 26px;
  font-weight: bold;
  color: #222;
  margin-bottom: 10px;
}

/* === Section Box === */
.uh-section {
  background: #ffffff;
  padding: 18px 25px;
  border-radius: 10px;
  width: 70%;
  max-width: 850px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.35);
  text-align: left;
  transition: 0.3s ease;
}

/* Section Titles */
.uh-title {
  font-size: 20px;
  font-weight: bold;
  color: #222;
  margin-bottom: 15px;
  border-left: 5px solid #007bff;
  padding-left: 10px;
}

/* === Cards (Summary) === */
.uh-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.uh-card {
  background: #ffffff;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  flex: 1;
  min-width: 120px;
  max-width: 180px;
  text-align: center;
  transition: 0.3s;
}

.uh-card:hover {
  transform: translateY(-3px);
}

.uh-card h3 {
  font-size: 15px;
  margin-bottom: 6px;
  color: #333;
}

.uh-card p {
  font-size: 18px;
  font-weight: bold;
  color: #007bff;
}

/* === Legend === */
.uh-legend {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 10px;
}

.uh-legend-item {
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  min-width: 70px;
  text-align: center;
}

.uh-legend-item.available { background: #28a745; }
.uh-legend-item.occupied { background: #dc3545; }
.uh-legend-item.reserved { background: #ffc107; color: #333; }
.uh-legend-item.special  { background: #17a2b8; }

/* === Parking Layout === */
.uh-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 15px;
  max-width: 600px;
  margin: 0 auto;
}

.uh-slot {
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-weight: bold;
  font-size: 16px;
  color: white;
  transition: 0.3s ease;
}

.uh-slot.available { background: #5ed87a; border: 2px solid #02a302; }
.uh-slot.occupied  { background: #f37b7b; border: 2px solid #fa0000; }
.uh-slot.reserved  { background: #ffc107; color: #333; }
.uh-slot.special   { background: #17a2b8; }

/* === Actions === */
.uh-actions {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}

.uh-actions button {
  padding: 10px 18px;
  border: none;
  border-radius: 6px;
  background: #007bff;
  color: white;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.uh-actions button:hover {
  background: #0056b3;
}

/* === Chart === */
.uh-chart {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 250px;
}

/* === Footer === */
.uh-footer {
  margin-top: 30px;
  padding: 15px;
  background: #222;
  color: white;
  width: 100%;
  text-align: center;
}

.uh-footer-container {
  max-width: 900px;
  margin: auto;
}

/* === RESPONSIVENESS === */

/* Large screens */
@media (min-width: 1200px) {
  .uh-title { font-size: 24px; }
  .uh-card p { font-size: 20px; }
  .uh-welcome { font-size: 30px; }
}

/* Tablets */
@media (max-width: 992px) {
  .uh-section { width: 85%; }
  .uh-title { font-size: 19px; }
  .uh-slot { height: 70px; width: 70px; font-size: 14px; }
}

/* Mobiles */
@media (max-width: 576px) {
  .uh-section { width: 95%; padding: 12px 15px; }
  .uh-title { font-size: 17px; }
  .uh-welcome { font-size: 22px; }
  .uh-card h3 { font-size: 13px; }
  .uh-card p { font-size: 16px; }
  .uh-slot { height: 60px; width: 60px; font-size: 13px; }
}
