/* === Cancel Booking Page === */
.cb-container {
  padding: 20px;
  padding-top: 60px;
  font-family: Arial, sans-serif;
  text-align: center;
}

.cb-title {
  margin-top: 36px;
  font-size: 22px;
  font-weight: bold;
}

.cb-no-bookings {
  margin-top: 20px;
  font-size: 18px;
  color: #555;
}

/* === Booking List === */
.cb-list {
  margin-top: 20px;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.cb-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f1f1f1;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 12px;
}

.cb-item-text {
  font-size: 15px;
  color: #333;
}

/* === Cancel Button === */
.cb-cancel-booking-btn {
  background: #e74c3c;
  color: white;
  border: none;
  padding: 6px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s;
}

.cb-cancel-booking-btn:hover {
  background: #c0392b;
}

/* === Confirmation Box === */
.cb-confirm-box {
  margin-top: 10px;
  text-align: center;
}

.cb-confirm-btn,
.cb-cancel-btn {
  padding: 6px 14px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin: 0 5px;
}

.cb-confirm-btn {
  background: #28a745;
  color: white;
}

.cb-confirm-btn:hover {
  background: #218838;
}

.cb-cancel-btn {
  background: #6c757d;
  color: white;
}

.cb-cancel-btn:hover {
  background: #5a6268;
}

/* 📱 Small screens (phones) */
@media (max-width: 480px) {
  .cb-title {
    font-size: 18px;
  }

  .cb-no-bookings {
    font-size: 16px;
  }

  .cb-list {
    max-width: 90%;
  }

  .cb-item {
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }

  .cb-cancel-booking-btn {
    width: 100%;
    padding: 10px;
  }
}

/* 📟 Tablets */
@media (min-width: 481px) and (max-width: 768px) {
  .cb-title {
    font-size: 20px;
  }

  .cb-list {
    max-width: 80%;
  }
}

/* 💻 Laptops */
@media (min-width: 769px) and (max-width: 1366px) {
  .cb-title {
    font-size: 22px;
  }
}
