/* ==============================
   Existing Dashboard Styles
   ============================== */
   .dashboard-container {
    margin-top: 80px; /* space for navbar */
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center; /* center everything horizontally */
    gap: 30px; /* spacing between sections */
  }
  
  .dashboard-title {
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #222;
    text-align: center;
  }
  
  .dashboard-cards {
    display: flex;
    justify-content: space-around;
    gap: 20px;
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    flex-wrap: nowrap; /* desktop: single line */
  }
  
  .card {
    flex: 1; /* equal width cards */
    min-width: 140px;
    background: #f9f9f9;
    padding: 15px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
  }
  
  .card h3 {
    margin-bottom: 8px;
    color: #007bff;
    font-size: 16px;
  }
  
  .card p {
    font-size: 18px;
    font-weight: bold;
    color: #222;
  }
  
  .sec123 {
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    width: 90%;
  }
  
  .Admin-titles {
    font-size: 20px;
    font-weight: bold;
    color: #222;
    margin-bottom: 15px;
    text-align: left;
    border-left: 5px solid #007bff;
    padding-left: 10px;
  }
  
  .action-buttons {
    display: flex;
    gap: 15px;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .action-buttons .btn {
    background: #007bff;
    color: white;
    padding: 10px 15px;
    border-radius: 6px;
    text-decoration: none;
    font-weight: 500;
    transition: 0.3s ease;
  }
  
  .action-buttons .btn:hover {
    background: #0056b3;
  }
  
  .recent-activity {
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    width: 90%;
    overflow-x: auto;
  }
  
  .recent-activity h2 {
    font-size: 20px;
    margin-bottom: 15px;
    border-left: 5px solid #007bff;
    padding-left: 10px;
    color: #222;
    text-align: left;
  }
  
  .recent-activity table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .recent-activity th,
  .recent-activity td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
  }
  
  .recent-activity th {
    background: #f5f5f5;
    color: #333;
  }
  
  /* ==============================
     Responsive Styles
     ============================== */
  
  /* Tablet (768px - 992px) */
  @media (max-width: 992px) {
    .dashboard-cards {
      flex-wrap: wrap; /* cards wrap on tablets */
      justify-content: space-between;
    }
    .card {
      flex: 1 1 45%; /* 2 cards per row */
      min-width: 120px;
    }
    .sec123,
    .recent-activity {
      width: 95%;
    }
  }
  
  /* Mobile (<768px) */
  @media (max-width: 768px) {
    .dashboard-title {
      font-size: 22px;
    }
    .dashboard-cards {
      flex-direction: column; /* single column on mobiles */
      gap: 15px;
      align-items: center;
    }
    .card {
      flex: 1 1 100%;
    }
    .Admin-titles,
    .recent-activity h2 {
      font-size: 18px;
    }
    .action-buttons {
      flex-direction: column;
      gap: 10px;
    }
    .action-buttons .btn {
      width: 100%; /* full-width buttons */
      text-align: center;
    }
    .recent-activity table th,
    .recent-activity table td {
      font-size: 14px;
      padding: 8px;
    }
  }
  