.bookings-container {
  padding: 20px;
  margin-top: 60px;
}

.page-title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Bookings Table */
.bookings-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 6px rgba(0,0,0,0.1);
  margin-bottom: 20px;
}

.bookings-table th,
.bookings-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: center;
}

.bookings-table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.actions-col {
  width: 200px;
}

.complete-btn {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  margin-right: 8px;
  cursor: pointer;
  transition: 0.2s;
}

.complete-btn:hover {
  background-color: #27ae60;
}

.cancel-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
}

.cancel-btn:hover {
  background-color: #c0392b;
}

.status-active {
  color: blue;
  font-weight: bold;
}

.status-completed {
  color: green;
  font-weight: bold;
}

.status-cancelled {
  color: red;
  font-weight: bold;
}

.no-bookings {
  text-align: center;
  padding: 20px;
  color: #777;
}

/* ==============================
   Responsive Table for Mobile
   ============================== */

/* Mobile (<768px) */
@media screen and (max-width: 767px) {
  .bookings-table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .bookings-table th,
  .bookings-table td {
    padding: 10px;
    font-size: 14px;
  }

  .actions-col {
    width: auto;
  }

  .complete-btn,
  .cancel-btn {
    padding: 5px 8px;
    font-size: 12px;
    margin-bottom: 5px;
  }

  .page-title {
    font-size: 22px;
  }
}

/* Tablet (768px - 991px) */
@media screen and (min-width: 768px) and (max-width: 991px) {
  .bookings-table th,
  .bookings-table td {
    padding: 10px;
    font-size: 15px;
  }

  .complete-btn,
  .cancel-btn {
    padding: 6px 10px;
    font-size: 13px;
  }
}

/* Desktop / Large Screens (992px+) */
@media screen and (min-width: 992px) {
  .bookings-table th,
  .bookings-table td {
    padding: 12px;
    font-size: 16px;
  }

  .complete-btn,
  .cancel-btn {
    padding: 6px 12px;
    font-size: 14px;
  }

  .page-title {
    font-size: 24px;
  }
}
