/* src/components/Message.css */

/* =========================
   Base Message Styles
   ========================= */
   .message {
    position: fixed;
    top: 20px;
    left: 50%; /* center horizontally */
    transform: translateX(-50%);
    padding: 12px 20px;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    z-index: 9999;
    box-shadow: 0 4px 6px rgba(0,0,0,0.2);
    opacity: 0.95;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 250px;
    max-width: 90%;
    text-align: center;

    /* Animation */
    animation: slideDown 0.5s ease forwards;
}

/* =========================
   Message Types
   ========================= */
.message.success { background-color: #28a745; }
.message.error   { background-color: #e74c3c; }
.message.info    { background-color: #3498db; }

/* =========================
   Children Container (for buttons)
   ========================= */
.message-children {
    margin-top: 10px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: center;
}

/* Buttons inside message */
.message-children button {
    padding: 6px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    transition: 0.2s;
}

.message-children button.confirm {
    background-color: #28a745;
    color: white;
}

.message-children button.cancel {
    background-color: #e74c3c;
    color: white;
}

.message-children button:hover {
    opacity: 0.85;
}

/* =========================
   Animations
   ========================= */
@keyframes slideDown {
    0% {
        transform: translate(-50%, -50px);
        opacity: 0;
    }
    100% {
        transform: translate(-50%, 0);
        opacity: 0.95;
    }
}

@keyframes slideUp {
    0% {
        transform: translate(-50%, 0);
        opacity: 0.95;
    }
    100% {
        transform: translate(-50%, -50px);
        opacity: 0;
    }
}

/* Add this class dynamically when hiding message */
.message.hide {
    animation: slideUp 0.5s ease forwards;
}

/* =========================
   Responsive Styles
   ========================= */

/* Small devices (≤ 480px) */
@media (max-width: 480px)and (min-width: 320px) {
    .message {
        width: 90%;
        min-width: unset;
        padding: 10px 15px;
        font-size: 14px;
    }

    .message-children button {
        padding: 5px 10px;
        font-size: 14px;
    }
}

/* Very small devices (≤ 320px) */
@media (max-width: 320px) {
    .message {
        padding: 8px 10px;
        font-size: 12px;
    }

    .message-children button {
        padding: 4px 8px;
        font-size: 12px;
    }
}
