.users-container {
  padding: 20px;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 10px;
}

.total-users {
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  margin-bottom: 15px;
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  width: 80%;
}

/* Users Table */
.users-table {
  width: 80%;
  border-collapse: collapse;
  background: white;
  border: 1px solid #ddd;
  box-shadow: 0px 3px 8px rgba(0,0,0,0.2);
  text-align: center;
}

.users-table th, .users-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: center;
}

.users-table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.actions-col {
  width: 120px;
}

.delete-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
}

.delete-btn:hover {
  background-color: #c0392b;
}

.no-users {
  text-align: center;
  padding: 20px;
  color: #777;
}

/* ==============================
   Responsive Styles
   ============================== */

/* Mobile (<768px) */
@media (max-width: 767px) {
  .users-table {
    width: 100%;
    display: block;
    overflow-x: auto; /* scroll horizontally */
    white-space: nowrap;
  }

  .users-table th,
  .users-table td {
    padding: 10px;
    font-size: 14px;
  }

  .actions-col {
    width: auto;
  }

  .delete-btn {
    padding: 5px 8px;
    font-size: 12px;
  }

  .total-users {
    width: 95%;
    font-size: 16px;
    padding: 15px;
  }

  .page-title {
    font-size: 20px;
  }
}

/* Tablet (768px - 991px) */
@media (min-width: 768px) and (max-width: 991px) {
  .users-table {
    width: 95%;
  }

  .users-table th, .users-table td {
    padding: 10px;
    font-size: 15px;
  }

  .delete-btn {
    padding: 6px 10px;
    font-size: 13px;
  }

  .total-users {
    width: 90%;
    font-size: 17px;
    padding: 18px;
  }

  .page-title {
    font-size: 22px;
  }
}

/* Desktop / Large Screens (992px+) */
@media (min-width: 992px) {
  .users-table {
    width: 80%;
  }

  .users-table th, .users-table td {
    padding: 12px;
    font-size: 16px;
  }

  .delete-btn {
    padding: 6px 12px;
    font-size: 14px;
  }

  .total-users {
    width: 80%;
    font-size: 18px;
    padding: 20px;
  }

  .page-title {
    font-size: 24px;
  }
}
