/* ==============================
   Existing Slots Styles
   ============================== */
   .slots-container {
    padding: 20px;
    margin-top: 60px;
    text-align: center;
  }
  
  .page-title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  /* ==============================
     Add Slot Form
     ============================== */
  .add-slot {
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    width: 80%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    box-sizing: border-box;
  }
  
  .add-slot input {
    padding: 6px;
    width: 100%;
    margin-bottom: 10px;
    box-sizing: border-box;
  }
  
  .add-slot button {
    padding: 6px 12px;
    width: 100%;
    cursor: pointer;
    margin-top: 5px;
  }
  
  /* ==============================
     Slots Table
     ============================== */
  .slots-table {
    width: 80%;
    margin: auto;
    border-collapse: collapse;
    background: white;
    border: 1px solid #ddd;
    box-shadow: 0px 2px 6px rgba(0,0,0,0.1);
  }
  
  .slots-table th, .slots-table td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: center;
  }
  
  .actions-col {
    width: 150px;
  }
  
  .delete-btn {
    background-color: #e74c3c;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .toggle-btn {
    background-color: #3498db;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 5px;
  }
  
  .delete-btn:hover { background-color: #c0392b; }
  .toggle-btn:hover { background-color: #2980b9; }
  
  .status-available {
    color: green;
    font-weight: bold;
  }
  
  .status-occupied {
    color: red;
    font-weight: bold;
  }
  
  /* ==============================
     Responsive Styles
     ============================== */
  
  /* Mobile (<768px) */
  @media (max-width: 767px) {
    /* Add Slot Form */
    .add-slot {
      width: 95%;
      padding: 15px;
      flex-direction: column;
      align-items: stretch;
    }
    .add-slot input {
      width: 100%;
      margin-bottom: 10px;
    }
    .add-slot button {
      width: 100%;
      margin-top: 5px;
    }
  
    /* Slots Table: horizontal scroll for mobile */
    .slots-table {
      width: 100%;
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  
    .slots-table th, .slots-table td {
      padding: 10px;
      font-size: 14px;
    }
  
    .actions-col {
      width: auto;
    }
  
    .delete-btn, .toggle-btn {
      padding: 5px 8px;
      font-size: 12px;
      margin-right: 5px;
    }
  }
  
  /* Tablet (768px - 991px) */
  @media (min-width: 768px) and (max-width: 991px) {
    /* Add Slot Form */
    .add-slot {
      width: 90%;
      padding: 20px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    .add-slot input {
      width: calc(50% - 10px);
      margin-bottom: 10px;
    }
    .add-slot button {
      width: auto;
      padding: 8px 16px;
      margin-top: 0;
      align-self: center;
    }
  
    /* Slots Table */
    .slots-table {
      width: 95%;
      display: table;
      overflow-x: visible;
    }
    .slots-table th, .slots-table td {
      padding: 10px;
      font-size: 15px;
    }
    .delete-btn, .toggle-btn {
      padding: 6px 10px;
      font-size: 13px;
    }
  }
  
  /* Desktop / Large Screens (992px+) */
  @media (min-width: 992px) {
    /* Add Slot Form */
    .add-slot {
      width: 80%;
      padding: 25px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    .add-slot input {
      width: calc(50% - 10px);
      margin-right: 10px;
      margin-bottom: 0;
    }
    .add-slot button {
      width: auto;
      padding: 8px 16px;
      margin-top: 0;
    }
  
    /* Slots Table */
    .slots-table {
      width: 80%;
    }
    .slots-table th, .slots-table td {
      padding: 12px;
      font-size: 16px;
    }
    .delete-btn, .toggle-btn {
      padding: 6px 12px;
      font-size: 14px;
    }
  }
  