/* Reports.css */

.reports-container {
  padding: 20px;
  margin-top: 60px;
  background-color: #f7f8fa;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}

.page-title {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 25px;
  color: #2c3e50;
}

/* Filter Section */
.filter-section {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.filter-section input[type="date"] {
  padding: 8px 12px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.filter-section button {
  padding: 8px 15px;
  border: none;
  border-radius: 5px;
  background-color: #3498db;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.filter-section button:hover {
  background-color: #2980b9;
}

/* Summary Cards */
.summary-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 30px;
  gap: 20px;
}

.card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.12);
  text-align: center;
  width: 220px;
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
}

.card h3 {
  margin-bottom: 10px;
  font-size: 18px;
  color: #34495e;
}

.card p {
  font-size: 22px;
  font-weight: bold;
  color: #2c3e50;
}

/* Charts Section */
.charts {
  display: flex;
  flex-direction: column; /* stack vertically on small screens */
  gap: 20px;
}

/* Each chart container for horizontal scroll */
.chart-wrapper {
  width: 100%; /* container stays in screen */
  overflow-x: auto; /* scroll horizontally if chart too wide */
  -webkit-overflow-scrolling: touch; /* smooth scroll on mobile */
  border-radius: 10px;
  padding-bottom: 5px; /* spacing for scrollbar */
}

.chart {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.12);
  text-align: center;
  min-width: 320px; /* chart width can exceed container */
  width: max-content; /* allow chart to grow horizontally */
  flex-shrink: 0; /* prevent shrinking */
  transition: 0.3s;
  box-sizing: border-box;
}

.chart h3 {
  margin-bottom: 15px;
  font-size: 18px;
  color: #34495e;
}

/* Reports Table */
.reports-table {
  width: 90%;
  margin: auto;
  border-collapse: collapse;
  background: white;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.12);
  margin-bottom: 30px;
}

.reports-table th, .reports-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}

.reports-table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.actions-col {
  width: 150px;
}

.view-btn {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
}

.view-btn:hover {
  background-color: #2980b9;
}

/* ==============================
   Responsive Design
   ============================== */

/* Mobile (<768px) */
@media screen and (max-width: 767px) {
  .summary-cards {
    flex-direction: column;
    align-items: center;
  }

  .card {
    width: 95%;
    padding: 15px;
  }

  .filter-section {
    flex-direction: column;
    align-items: center;
  }

  .filter-section input[type="date"],
  .filter-section button {
    width: 90%;
    margin-bottom: 10px;
  }

  /* Stack charts vertically */
  .charts {
    flex-direction: column;
    gap: 20px;
  }

  /* Each chart horizontally scrollable inside its container */
  .chart-wrapper {
    overflow-x: auto;
  }

  .chart {
    min-width: 400px; /* chart width larger than container */
  }

  .reports-table {
    width: 100%;
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .reports-table th,
  .reports-table td {
    padding: 10px;
    font-size: 14px;
  }

  .actions-col {
    width: auto;
  }

  .view-btn {
    padding: 5px 8px;
    font-size: 12px;
  }

  .page-title {
    font-size: 22px;
  }
}

/* Tablet (768px - 991px) */
@media screen and (min-width: 768px) and (max-width: 991px) {
  .summary-cards {
    justify-content: space-around;
  }

  .card {
    width: 48%;
  }

  .chart-wrapper {
    overflow-x: auto;
  }

  .chart {
    min-width: 400px;
  }

  .reports-table {
    width: 95%;
  }

  .reports-table th,
  .reports-table td {
    padding: 10px;
    font-size: 15px;
  }
}

/* Desktop / Large Screens (992px+) */
@media screen and (min-width: 992px) {
  .summary-cards,
  .charts {
    flex-wrap: nowrap;
  }

  .card {
    width: 220px;
  }

  .chart {
    width: 400px;
    min-width: 400px;
  }

  .reports-table {
    width: 90%;
  }

  .reports-table th,
  .reports-table td {
    padding: 12px;
    font-size: 16px;
  }

  .view-btn {
    padding: 6px 12px;
    font-size: 14px;
  }

  .page-title {
    font-size: 28px;
  }
}
